<template>
  <section class="print-box">
      <el-row class="print-inner">
        <el-col :span="24" class="print-title">
        处方笺
        </el-col>
        <el-col :span="24" class="print-patient">
          <span>姓名: {{patinet.emName }}</span>
          <span>性别: {{patinet.emSex }}</span>
          <span>年龄：{{patinet.emAge }}</span>
          <span>科室：{{patinet.deptName }}</span>
          <span>病历号：{{patinet.registrationNum }}</span>
        </el-col>
        <!-- <el-col class="case-info" v-for="node in nodeList" :key="node.title">
          <span class="title">【{{node.title}}】</span> {{node.data}}
        </el-col> -->
        <el-col class="case-info-chufang">
          <el-col :span="6" class="case-info-chufang-left">
            <p>
              <span>临床诊断:</span>
              <br>
              {{chineseMedicineName}}
              <br v-if="chineseMedicineName">
               <!-- {{westernMedicineName}}
               <br v-if="westernMedicineName"> -->
               {{dialecticalName}}
               <br v-if="dialecticalName">
               {{mainSymptomName}}
            </p>
            <p>
              <span>医保提示:</span>
              <br>
              开药量急性病不得超过三日量，慢性病不超过七日量，行动不便的可开两周量。患高血压、糖尿病、冠心病、慢性肝炎、肝硬化、结核病、精神病、癌症、脑血管病、前列腺肥大疾病，且病情稳定需长期服用同一类药物的，可放宽到不超过一个月量
            </p>
          </el-col>
          <el-col :span="18" class="case-info-chufang-right">
            <span style="font-size:20px">Rp:</span><br>
            <p>
              <span>【处方】:</span> <br><br>
             <i style="padding-left: 10px">{{caoyaoItems}}</i>
            </p>
            <p>
              <br>
               <span class="all-space-show"> 【剂 数】:</span> {{usageCount}}剂 <br>
               <span class="all-space-show"> 【代 煎】:</span> {{daiJian}}<br>
               <span class="all-space-show"> 【用 法】:</span> {{fangjiUsage}} <br>
               <span class="all-space-show"> 【医 嘱】:</span> {{advice}} <br>
               <br>
               <span class="all-space-show">   医师签名(签章)：__________________</span>
            </p>
          </el-col>
        </el-col>
        <el-col class="print-footer">
            <span>药品金额: </span>
            <span>计价员:</span>
            <span>审核/调配签名：</span>
            <span>核对/发药签名：</span>
            <span></span>
        </el-col>
        <el-col style="margin-top: 5px;">
          药师提示： 1、请遵医嘱服药；2、请在窗口清点药品；3、处方当日在本院有效；4、发出药品不予退还
          </el-col>
        <el-col :offset="16" style="margin-top:20px">

        </el-col>
        <el-col>
              <el-button @click="printPage" v-if='buttonShow'>开始打印</el-button>
        </el-col>
    </el-row>
  </section>
</template>
<script>
import {
  getEmpBasicInfo,
  getSelectPatientInfoTable,
  printChuFang,
  getEmpBasicMainEntyTable, // 患者详情
} from '../../api'
export default {
  data(){
    return{
      tableData1: [{data:1}],
      tableData2: [],
      info: '',
      dataChack1: false,
      dataChack2: false,
      nodeList: [],// 病例信息
      patinet: {} , // 患者信息
      buttonShow: false,
      chineseMedicineName: '', // 中医病名
      westernMedicineName: '', // 西医病名
      dialecticalName: '', // 症形
      mainSymptomName: '', // 主症
      caoyaoItems: '', // 草药显示信息
      advice: '', // 医嘱
      fangjiUsage: '', // 用法
      usageCount: '', // 数量
      daiJian: '否', // 代 煎
    }
  },
  computed:{
    isFinish(){
      if(this.dataChack1 && this.dataChack2){
        return true
      }else{
        return false
      }
    }
  },
  watch:{
    isFinish(curVal){
      if(curVal){
       // this.printPage()
       this.buttonShow = true
      }
    }
  },
  methods:{
    printPage(){
      this.buttonShow = false
      let timer = window.setTimeout(()=>{
        window.print()
        clearTimeout(timer)
        window.close()
        this.buttonShow = true
      },100)
    },
    getChufangList(eid){ // 获取处方数据
      printChuFang({
        drugFlag: 0,
        empBasicMainID: eid,
      }).then(res=>{
        if(res.data.code == '0'){
          let data = res.data.data
          this.fangjiUsage = data.fangjiUsage
          this.advice = data.advice
          this.usageCount = data.usageCount
          if(data.daiJian){
            this.daiJian = '是'
          }else{
            this.daiJian = '否'
          }
          data.fangJiDetailList.forEach(element=>{
            this.caoyaoItems += element.drugName + ' ' + element.quanitity + 'g, '
          })
        }
      })
    }
  },
  mounted(){
    let id = this.$route.query.id || sessionStorage.getItem('current_registrationNum')
    if(id){
        getEmpBasicInfo({
        registrationNum: id,
        drugFlag: 0
        }).then(res=>{
          return
        let str = res.data.data[0].returnValueStr
        let reg = /(【[\u4e00-\u9fa5]*】[^【]+)/g
        let arr =str.match(reg)
        let tpl = ''
        let p = document.querySelector('#result')
        arr.forEach(element => {
          let temp = element.split('】')
          let span = null
          this.nodeList.push({
            title: temp[0].substring(1),
            data: temp[1] || '',
          })
          this.dataChack1 = true
        })
      // this.printPage()
      })
      this.dataChack1 = true
      getEmpBasicMainEntyTable({registrationNum: id}).then(res=>{
        if(res.data.code == '0'){
          let info = res.data.data[0]
          this.chineseMedicineName = info.chineseMedicineName
          this.westernMedicineName = info.westernMedicineName
          this.dialecticalName = info.dialecticalName
          this.mainSymptomName = info.mainSymptomName
        }
      })
      getSelectPatientInfoTable({registrationNum: id}).then(res=>{
        if(res.data.code=="0"){
          this.patinet.emName = res.data.data.emName
          this.patinet.deptName = res.data.data.deptName
          this.patinet.registrationNum = res.data.data.registrationNum
          this.patinet.emSex = res.data.data.emSex=='1'? '男' : '女'
          this.patinet.emAge = res.data.data.emAge
          this.dataChack2 = true
          this.getChufangList(res.data.data.empBasicMainID)
        }
      })
    }else{
      this.$message.warning('无效访问 请正常进入!')
    }

  }
}
</script>
<style scoped>
.case-info{
  font-size: 14px;
  color: #555;
  line-height: 20px;
}
.case-info .title{
  font-size: 15px;
  color: #333;
}
.case-info .title::before{
  display: block;
  content: ' ';
  width: 100%;
  height: 12px;
}
.case-info .el-table{
  margin: 20px 6px;
}
.case-info .el-table i{
  color: #3E85FA;
  font-size: 22px;
  cursor: pointer;
}
.print-box{
  width: 100%;
  background-color: #fff;
  height: 100vh;
}
.print-inner{
  width: 960px;
  padding-top: 40px;
  padding-left: 40px;
}
.print-inner .print-title{
  font-size: 24px;
  text-align: center;
}
.print-patient{
  display: flex;
  justify-content:space-between;
  margin: 20px 0;
  margin-bottom: 0;
  border-bottom: 1px solid #333;
}
.print-patient span{
  line-height: 24px;
}
.print-footer{
  display: flex;
  justify-content:space-between;
  padding-top: 6px;
  border-top: 1px solid #333;
}
.case-info-chufang{
  border-left: 1px solid #333;
  border-right: 1px solid #333;
}
.case-info-chufang-left,.case-info-chufang-right{
  padding: 16px 6px;
}
.case-info-chufang-left{
  border-right: 1px solid #333;
}
.case-info-chufang span{
  font-weight: bold;
}
</style>




